#card-home-red {
  display: flex;
  align-items: center;
  height: calc(100vh - 50px);
  position: absolute;
  left: 5%;
  width: 22%;
  user-select: none;

  .card {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    background: var(--black-accent-color2);
    padding: 40px 25px;
    cursor: pointer;

    header {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      margin-bottom: 30px;
      color: var(--red-primary-color);

      .year {
        font-weight: normal;
        font-size: calc(7px + 0.75vw);
        display: block;
        text-align: center;
        margin-bottom: -10px;
      }

      .title {
        font-size: calc(34px + 0.75vw);
        text-decoration: underline;
      }
    }

    .text {
      font-size: calc(4px + 0.75vw);
      line-height: 1.3em;
      color: var(--text-color);
    }

    button.btn {
      width: 100%;
      background: var(--black-accent-color2);
      border: 3px solid var(--red-primary-color);
      color: var(--text-color);
      font-size: calc(6px + 0.75vw);
      padding: 15px 0;
      margin-top: 50px;
      cursor: pointer;
      transition: background 0.2s, transform 0.05s;
    }

    &:hover .btn {
      background: var(--red-primary-color);
    }

    &:active .btn {
      transform: scale(0.98);
      background: var(--red-primary-color);
    }

    .corners {
      .square {
        position: absolute;
        display: block;
        background: var(--red-primary-color);
        height: 40px;
        width: 40px;
        z-index: -1;

        &:nth-child(1) {
          left: -5px;
          top: -5px;
        }

        &:nth-child(2) {
          right: -5px;
          top: -5px;
        }

        &:nth-child(3) {
          left: -5px;
          bottom: -5px;
        }

        &:nth-child(4) {
          right: -5px;
          bottom: -5px;
        }
      }
    }
  }
}

@media (max-width: 1600px) {
  #card-home-red {
    width: 25%;
  }
}

@media (max-width: 1280px) {
  #card-home-red {
    width: 27.5%;
  }
}

@media (max-width: 1024px) {
  #card-home-red {
    align-items: end;
    bottom: 10px;
    width: 98%;
    height: 32%;
    left: 1%;

    .card {
      height: 100%;
      padding: 17.5px 25px;

      header {
        margin-bottom: 15px;

        .year {
          font-size: calc(10px + 0.8vw);
        }

        .title {
          font-size: calc(34px + 0.8vw);
        }
      }

      .text {
        width: 90%;
        font-size: calc(8px + 0.8vw);
      }

      button.btn {
        margin-top: 20px;
        width: 90%;
        border: 2px solid var(--red-primary-color);
        padding: 10px 0;
        font-size: calc(8px + 0.8vw);
      }
      .corners {
        .square {
          &:nth-child(1) {
            left: -3px;
            top: -3px;
          }

          &:nth-child(2) {
            right: -3px;
            top: -3px;
          }

          &:nth-child(3) {
            left: -3px;
            bottom: -3px;
          }

          &:nth-child(4) {
            right: -3px;
            bottom: -3px;
          }
        }
      }
    }
  }
}

@media (max-width: 768px) {
  #card-home-red {
    height: 28%;
    bottom: 5px;

    .card {
      .text {
        width: 100%;
        font-size: calc(8px + 0.8vw);
      }

      button.btn {
        margin-top: 20px;
        width: 100%;
        border: 2px solid var(--red-primary-color);
        padding: 10px 0;
        font-size: calc(8px + 0.8vw);
      }
    }
  }
}
